import * as THREE from 'three'
import ReactDOM from 'react-dom'
import React, { useRef, useState, useEffect } from 'react'
import { apply, Canvas, useRender, useThree } from 'react-three-fiber'
import { useSprings, a } from 'react-spring/three'
import * as resources from './resources/index'
import './styles.css'
// Make extra stuff available as native-elements (<effectComposer />, etc.)
apply(resources)

let number = 30
let colors = ['#A2CCB6', '#FCEEB5', '#EE786E', '#e0feff']
const random = () => {
  const r = Math.random()
  return {
    position: [30 - Math.random() * 60, 30 - Math.random() * 60, 0],
    color: colors[Math.round(Math.random() * (colors.length - 1))],
    scale: [1 + r * 10, 1 + r * 10, 1],
    rotation: [0, 0, THREE.Math.degToRad(Math.round(Math.random()) * 45)]
  }
}

function Content() {
  const { viewport } = useThree()
  const aspect = viewport.width / 6
  const [springs, set] = useSprings(number, i => ({ from: random(), ...random(), config: { mass: 20, tension: 500, friction: 200 } }))
  useEffect(() => void setInterval(() => set(i => ({ ...random(), delay: i * 50 })), 4000), [])
  return springs.map(({ color, ...props }, index) => (
    <a.mesh key={index} {...props}>
      <planeBufferGeometry attach="geometry" args={[0.1 + Math.random() * aspect, 0.1 + Math.random() * aspect]} />
      <a.meshPhongMaterial attach="material" color={color} />
    </a.mesh>
  ))
}

function Effect() {
  const composer = useRef()
  const { scene, gl, size, camera } = useThree()
  useEffect(() => void composer.current.setSize(size.width, size.height), [size])
  useRender(() => composer.current.render(), true)
  return (
    <effectComposer ref={composer} args={[gl]}>
      <renderPass attachArray="passes" scene={scene} camera={camera} />
      <waterPass attachArray="passes" factor={1} />
      <shaderPass attachArray="passes" args={[resources.FXAAShader]} material-uniforms-resolution-value={[1 / size.width, 1 / size.height]} renderToScreen />
    </effectComposer>
  )
}

let capturedImage
export default function App() {
  const [isPaused, setIsPaused] = useState(false)
  const [backgroundColor, setBackgroundColor] = useState('#A2CCB6')
  const [version, setVersion] = useState(0)
  useEffect(() => {
    const messageHandler = event => {
      if (event.data.type === 'pause') {
        capturedImage = document.querySelector('#canvas-wrapper > canvas').toDataURL('image/png')
        setIsPaused(true)
        return
      }
      if (event.data.type === 'unpause') {
        setIsPaused(false)
        return
      }
      if (event.data.type === 'render') {
        const { payload } = event.data
        let isPaletteSame = true
        if (payload.palette && payload.palette.length) {
          payload.palette.forEach((color, index) => {
            if (color !== colors[index]) {
              isPaletteSame = false
            }
          })
          if (!isPaletteSame) {
            colors = payload.palette
            setVersion(version + 1)
            return
          }
        }
        if (payload.backgroundColor && payload.backgroundColor !== backgroundColor) {
          setBackgroundColor(payload.backgroundColor)
          return
        }
        if (payload.itemsNum !== number) {
          number = payload.itemsNum
          setVersion(version + 1)
          return
        }
        return
      }
      if (event.data.type === 'download') {
        const canvas = document.querySelector('#canvas-wrapper > canvas')
        const pausedImage = document.querySelector('paused-image')
        capturedImage = (canvas || pausedImage).toDataURL('image/png')
        window.parent.postMessage({ type: 'download', image: capturedImage }, '*')
        return
      }
    }
    window.addEventListener('message', messageHandler)
    return () => window.removeEventListener('message', messageHandler)
  }, [version, backgroundColor, number])
  if (isPaused) {
    return <img id="paused-image" alt="" className="main" src={capturedImage} />
  }
  return (
    <div className="main" style={{ color: 'fff' }}>
      <Canvas key={version} id="canvas-wrapper" gl={{ preserveDrawingBuffer: true }} style={{ background: backgroundColor }} camera={{ position: [0, 0, 30] }}>
        <ambientLight intensity={0.5} />
        <spotLight intensity={0.5} position={[300, 300, 4000]} />
        <Effect />
        <Content key={version} />
      </Canvas>
    </div>
  )
}

ReactDOM.render(<App />, document.getElementById('root'))
